import { createSlice } from "@reduxjs/toolkit";

export const filterSlice = createSlice({
  name: "filters",
  initialState: {
    // user: null,
    elements: "",
  },
  reducers: {
    getDataElements: (state, action) => {
      state.elements = action.payload.elements;
    },
    // userElements: (state) => {
    //   state.name = "username";
    // },
  },
});

export const { getDataElements } = filterSlice.actions;
export const selectFilter = (state) => state.filter.elements;
export default filterSlice.reducer;
