import React, { useState, useEffect, useCallback } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { tutorialSlice } from "../slices";
import ComponentsAPI from "../APIs/componentsAPI";
import "./style.css";
// import "bootstrap/dist/css/bootstrap.min.css";

// import DeleteUser from "../features/deleteUser";

const TutorialsList = () => {
  const [currentTutorial, setCurrentTutorial] = useState(null);
  const [currentIndex, setCurrentIndex] = useState(-1);
  const [searchTitle, setSearchTitle] = useState("");

  const items = useSelector((state) => state.tutorials);
  const [filteredItems, setFilteredItems] = useState([]);

  // const [items, setTutorials] = useState(getTutorials);
  const dispatch = useDispatch();

  const onChangeSearchTitle = (e) => {
    const searchTitle = e.target.value;
    if (searchTitle !== "") {
      const filteredResults = items.filter((item) => {
        // --------------> function for entire title  search
        return Object.values(item.title)
          .join("")
          .toLowerCase()
          .includes(searchTitle.toLowerCase());
        // -------------->old function for title start search
        // .startWith(searchTitle.toLowerCase(), 1);
        // Use the toLowerCase() method to make it case-insensitive
      });
      setFilteredItems(filteredResults);
    } else {
      setFilteredItems(items);
      // If the text field is empty, show all users
    }

    setSearchTitle(searchTitle);
  };

  const initFetch = useCallback(() => {
    dispatch(tutorialSlice.retrieveTutorials());
    // onChangeSearchTitle();
  }, [dispatch]);

  useEffect(() => {
    initFetch();
    // onChangeSearchTitle();
  }, [initFetch]);

  const refreshData = () => {
    setCurrentTutorial(null);
    setCurrentIndex(-1);
  };
  //
  //   const setActiveTutorial = (item, index) => {
  //     setCurrentTutorial(item);
  //     setCurrentIndex(index);
  //   };

  const removeAllTutorials = () => {
    dispatch(tutorialSlice.deleteAllTutorials())
      .then(() => {
        refreshData();
      })
      .catch((e) => {
        console.log(e);
      });
  };

  const findByTitle = (e) => {
    e.preventDefault(e);
    refreshData();
    useCallback(() => {
      dispatch(tutorialSlice.findTutorialsByTitle({ title: searchTitle }));
    }, [dispatch]);
  };
  return (
    <div className="login-form">
      {/* Search box */}
      <div className="col-md-8">
        <div className="input-group mb-3">
          <input
            type="text"
            className="input"
            placeholder="Search by name"
            // value={searchTitle}
            icon="search"
            onChange={onChangeSearchTitle}
          />

          <button
            className="btn btn-outline-primary search-btn"
            type="button"
            onClick={findByTitle}>
            Search
          </button>
        </div>
      </div>
      {/* Users List  */}
      <div className="col-md-6">
        <h4>Users List</h4>
        <ComponentsAPI.SearchFilterUI
          searchInputName={searchTitle}
          filteredItemsName={filteredItems}
          setCurrentIndexName={currentIndex}
          fetchedListName={items}
        />

        <button
          className="m-3 btn btn-sm btn-danger"
          onClick={removeAllTutorials}>
          Remove All
        </button>
      </div>
      <div className="col-md-6">
        {currentTutorial ? (
          <div>
            <h4>User</h4>
            <div>
              <label>
                <strong>Name:</strong>
              </label>{" "}
              {currentTutorial.name}
            </div>
            <div>
              <label>
                <strong>Email:</strong>
              </label>{" "}
              {currentTutorial.email}
            </div>
            <div>
              <label>
                <strong>Status:</strong>
              </label>{" "}
              {currentTutorial.published ? "Published" : "Pending"}
            </div>

            <Link
              to={"/users/" + currentTutorial.id}
              className="badge badge-warning">
              Edit
            </Link>
          </div>
        ) : (
          <div>
            <br />
            <p>Please click on a Tutorial...</p>
          </div>
        )}
      </div>
    </div>
  );
};

export default TutorialsList;
