import { combineReducers, configureStore } from "@reduxjs/toolkit";

import authReducer from "./slices/auth";
import messageReducer from "./slices/message";
import tutorialReducer from "./slices/tutorials";

messageReducer;

/**
 *@name {reducer}
 *@author {Haleem}
 *@brief {Reducer is a javascript object that contains components slices}
 *@description
 *
 */
const reducer = combineReducers({
  auth: authReducer,
  tutorials: tutorialReducer,
  message: messageReducer,
});

/**
 * @brief `{store}` will bring Actions and Reducers together and hold the Application state.
The Redux Toolkit configureStore() function automatically:
enable the Redux DevTools Extension.
sets up the thunk middleware by default, so you can immediately write thunks without more configuration.
 */
const store = configureStore({
  reducer: reducer,
  devTools: true,
});

// console.log(store.getState());
export default store;
