import React, { useEffect, useCallback } from "react";
import "./AdminPage.css";

import { useDispatch, useSelector } from "react-redux";

import "bootstrap/dist/css/bootstrap.min.css";
// import "./AuthApp.css";

import eventBus from "../common/EventBus";
import { authSlice } from "../../slices";
// import { Redirect } from "react-router-dom";
import ComponentsAPI from "../../APIs/componentsAPI";
/**
 * If the user is logged in, then render the NavBar component. If the user is logged out, then redirect
 * to the home page.
 * @returns The user is being returned as logged out.
 */
const LandingPage = () => {
  const { user: currentUser } = useSelector((state) => state.auth);
  const { isLoggedIn } = useSelector((state) => state.auth);

  const dispatch = useDispatch();

  const logOut = useCallback(() => {
    dispatch(authSlice.logout());
  }, [dispatch]);

  useEffect(() => {
    eventBus.on("logout", () => {
      logOut();
    });

    return () => {
      eventBus.remove("logout");
    };
    // console.log("USER LOGGED OUT");
  }, [currentUser, logOut]);

  if (!isLoggedIn) {
    return (
      <>
        <ComponentsAPI.NavBar />
        {/* <Redirect to="/" /> */}

        {console.log("USER IS LOGGED OUT")}
      </>
    );
  }

  return <ComponentsAPI.NavBar currentUser={currentUser} logOut={logOut} />;
};

export default LandingPage;
