import React from "react";
// import "./style.css";
import {
  // HomeContainer,
  HomeInformation,
  HomeTitle,
  HomeInfo,
  HomeDesc,
  Span,
  HomeBtn,
  HomeContainer,
} from "./style";
import ComponentsAPI from "../../APIs/componentsAPI";
import { useState } from "react";
import { useEffect } from "react";
import axios from "axios";
import LinksAPI from "../../APIs/LinksAPI";

const Home = () => {
  const [title, setTitle] = useState("");
  const [info, setInfo] = useState("");
  const [buttonText, setButtonText] = useState("");
  const [firstPart, setFirstPart] = useState("");
  const [spanPart, setSpanPart] = useState("");
  const [lastPart, setLastPart] = useState("");

  useEffect(() => {
    axios.get(LinksAPI.homePageData).then((res) => {
      setTitle(res.data.homeTitle);
      setInfo(res.data.homeInfo);
      setButtonText(res.data.homeButton);
      setFirstPart(res.data.firstPart);
      setSpanPart(res.data.spanPart);
      setLastPart(res.data.lastPart);
    });
  }, []);

  return (
    <>
      <ComponentsAPI.HomeItems />
      <HomeContainer className="home">
        <HomeInformation>
          <HomeTitle>{title}</HomeTitle>
          <HomeInfo>{info}</HomeInfo>
          <HomeDesc>
            {firstPart} <Span> {spanPart} </Span> {lastPart}
          </HomeDesc>
          {/* <HomeDesc>
            Iam a professional <Span>UX Designer</Span> and Front-End Developer
            creating modern and resposive designs to Web and Mobile. Let us work
            together. Thank you.
          </HomeDesc> */}

          {/* <HomeDescContent /> */}
          <HomeBtn> {buttonText} </HomeBtn>
        </HomeInformation>
      </HomeContainer>
    </>
  );
};

export default Home;
