import React from "react";

/** * It's a function that takes in a currentUser and a logOut function and returns a list item with a * link to the user's profile and a link to log out. * @returns A React component. */

export const AdminUserUI = ({ currentUser, logOut }) => {
  return (
    <>
      {/* <li>
        <a href="/portfolio">Portfolio</a>
      </li> */}
      <li className="services">
        <a href="/#">Admin</a>
        <ul className="dropdown">
          <li>
            <a href="/user">{currentUser.username}</a>
          </li>

          <li>
            <a href="/logout" onClick={logOut}>
              Logout
            </a>
          </li>
        </ul>
      </li>
      <li className="services">
        <a href="/#">Posts</a>
        <ul className="dropdown">
          <li>
            <a href="/tutorials">All Posts</a>
          </li>
          <li>
            <a href="/tutorials/add">New Post</a>
          </li>
          <li>
            <a href="/tutorials/:id">Show Post</a>
          </li>
        </ul>
      </li>
    </>
  );
};

/**
 * It returns a list item with a link to the admin page, and a dropdown menu with two links to the
 * login and register pages
 * @returns A React component.
 */

export const NormalUserUI = () => {
  return (
    <li className="services">
      <a href="/#">Admin</a>
      <ul className="dropdown">
        <li>
          <a href="/login">Login</a>
        </li>
        <li>
          <a href="/register">Register</a>
        </li>
      </ul>
    </li>
  );
};
