import React, { useState, useEffect, memo } from "react";
import { useDispatch } from "react-redux";

import TutorialService from "../Services/TutorialService";
import { tutorialSlice } from "../slices";
import "./style.css";
const Tutorial = (props) => {
  const initialTutorialState = {
    id: null,
    title: "",
    description: "",
    published: false,
  };
  const [currentTutorial, setCurrentTutorial] = useState(initialTutorialState);
  const [message, setMessage] = useState("");

  const dispatch = useDispatch();

  const getTutorial = (id) => {
    TutorialService.get(id)
      .then((response) => {
        setCurrentTutorial(response.data);
      })
      .catch((e) => {
        console.log(e);
      });
  };

  useEffect(() => {
    getTutorial(props.match.params.id);
  }, [props.match.params.id]);

  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setCurrentTutorial({ ...currentTutorial, [name]: value });
  };

  const updateStatus = (status) => {
    const data = {
      id: currentTutorial.id,
      title: currentTutorial.title,
      description: currentTutorial.description,
      published: status,
    };

    dispatch(tutorialSlice.updateTutorial({ id: currentTutorial.id, data }))
      .unwrap()
      .then((response) => {
        console.log(response);
        setCurrentTutorial({ ...currentTutorial, published: status });
        setMessage("The status was updated successfully!");
      })
      .catch((e) => {
        console.log(e);
      });
  };

  const updateContent = () => {
    dispatch(
      tutorialSlice.updateTutorial({
        id: currentTutorial.id,
        data: currentTutorial,
      })
    )
      .unwrap()
      .then((response) => {
        console.log(response);
        setMessage("The tutorial was updated successfully!");
        props.history.push("/tutorials");
      })
      .catch((e) => {
        console.log(e);
      });
  };

  const removeTutorial = () => {
    dispatch(tutorialSlice.deleteTutorial({ id: currentTutorial.id }))
      .unwrap()
      .then(() => {
        props.history.push("/tutorials");
      })
      .catch((e) => {
        console.log(e);
      });
  };

  return (
    <div>
      {console.log("Tutorial Render")}
      {currentTutorial ? (
        <div className="edit-form">
          <h4>Tutorial</h4>
          <form>
            <div className="form-group">
              <label htmlFor="title">Title</label>
              <input
                type="text"
                className="form-control"
                id="title"
                name="title"
                value={currentTutorial.title}
                onChange={handleInputChange}
              />
            </div>
            <div className="form-group">
              <label htmlFor="description">Description</label>
              <input
                type="text"
                className="form-control"
                id="description"
                name="description"
                value={currentTutorial.description}
                onChange={handleInputChange}
              />
            </div>

            <div className="form-group">
              <label>
                <strong>Status:</strong>
              </label>
              {currentTutorial.published ? "Published" : "Pending"}
            </div>
          </form>

          {currentTutorial.published ? (
            <button
              className="badge badge-primary mr-2"
              onClick={() => updateStatus(false)}>
              UnPublish
            </button>
          ) : (
            <button
              className="badge badge-primary mr-2"
              onClick={() => updateStatus(true)}>
              Publish
            </button>
          )}

          <button className="badge badge-danger mr-2" onClick={removeTutorial}>
            Delete
          </button>

          <button
            type="submit"
            className="badge badge-success"
            onClick={updateContent}>
            Update
          </button>
          <p>{message}</p>
        </div>
      ) : (
        <div>
          <br />
          <p>Please click on a User...</p>
        </div>
      )}
    </div>

    // <EditItemUi
    //   currentItem={currentTutorial}
    //   // itemHeader={"Tutorial"}
    //   firstField="title"
    //   secondField="description"
    //   thirdField="published"
    //   updateStatus={updateStatus}
    //   handleInputChange={handleInputChange}
    //   removeTutorial={removeTutorial}
    //   updateContent={updateContent}
    //   message={message}
    // />
  );
};

export default memo(Tutorial);
