const StaticList = (props) => {
  let index = props.index;
  return (
    <li
      className={
        props.itemName + (index === props.setCurrentIndexName ? "active" : "")
      }
      key={index}>
      <span className="user-name">{props.item[props.variable]}</span>
      <Link to={"/tutorials/" + props.item.id} className="badge badge-warning">
        Edit
      </Link>
    </li>
  );
};

const SearchFilterUI = ({
  filteredItemsName,
  setCurrentIndexName,
  searchInputName,
  fetchedListName,
  itemName,
  variable,
  link,
}) => {
  return (
    <ul className="user-list">
      {searchInputName.length > 1
        ? filteredItemsName.map((item, index) => {
            return (
              <StaticList
                itemName={itemName}
                setCurrentIndexName={setCurrentIndexName}
                variable={variable}
                item={item}
                index={index}
                link={link}
              />
              //   <li
              //     className={
              //       itemName + (index === setCurrentIndexName ? "active" : "")
              //     }
              //     key={index}>
              //     <span className="user-name">{item[variable]}</span>
              //     <Link to={link + item.id} className="badge badge-warning">
              //       Edit
              //     </Link>
              //   </li>
            );
          })
        : fetchedListName.map((item, index) => (
            <StaticList
              itemName={itemName}
              setCurrentIndexName={setCurrentIndexName}
              variable={variable}
              item={item}
              index={index}
              link={link}
            />
            // <li
            //   className={
            //     itemName + (index === setCurrentIndexName ? "active" : "")
            //   }
            //   key={index}>
            //   <span className="user-name">{item[variable]}</span>
            //   <Link to={link + item.id} className="badge badge-warning">
            //     Edit
            //   </Link>
            // </li>
          ))}
    </ul>
  );
};

export default SearchFilterUI;
