/** @format */

import React, { useState, useEffect } from "react";
import axios from "axios";
import "./style.css";
import {
  PortfolioSection,
  PortfolioTitle,
  PortfolioTitleSpan,
  PortfolioList,
  PortfolioItem,
  ImageWrapper,
  Image,
  Overlay,
  OverlaySpan,
} from "./style";
import LinksAPI from "../../APIs/LinksAPI";
const Portfolio = () => {
  const [images, setImages] = useState([]);
  const [titles, setTitles] = useState([]);

  useEffect(() => {
    // axios.get("/js/portfolioData.json").then((res) => {
    axios.get(LinksAPI.portfolioData).then((res) => {
      setImages(res.data.portfolio);
      setTitles(res.data.portfolio);
    });
  }, []);

  const PortfolioImages = images.map(function (imageItem) {
    return (
      <ImageWrapper key={imageItem.id}>
        <Image src={imageItem.image} alt="" />
        <Overlay>
          <OverlaySpan>{imageItem.hoverText}</OverlaySpan>
        </Overlay>
      </ImageWrapper>
    );
  });
  const PortfolioTitles = titles.map(function (titleItem) {
    return (
      <PortfolioItem key={titleItem.id} active={titleItem.active}>
        {titleItem.title}
      </PortfolioItem>
    );
  });

  return (
    <PortfolioSection>
      <PortfolioTitle>
        <PortfolioTitleSpan>My</PortfolioTitleSpan> Portfolio
      </PortfolioTitle>
      <PortfolioList>{PortfolioTitles}</PortfolioList>
      {/* <PortfolioList>
        <PortfolioItem active>All</PortfolioItem>
        <PortfolioItem>HTML</PortfolioItem>
        <PortfolioItem>Photoshop</PortfolioItem>
        <PortfolioItem>Wordpress</PortfolioItem>
        <PortfolioItem>Mobile</PortfolioItem>
      </PortfolioList> */}

      <div className="Box">{PortfolioImages}</div>
    </PortfolioSection>
  );
};

export default Portfolio;
