import axios from "axios";
import React, { useEffect, useState } from "react";
import LinksAPI from "../../APIs/LinksAPI";
import { FooterSection, FooterSectionDesc, FooterDate } from "./style";

const Footer = () => {
  const [creatorName, setCreatorName] = useState("");
  const [creatorLink, setCreatorLink] = useState("");
  const [date, setDate] = useState("");

  useEffect(() => {
    // axios.get("/js/portfolioData.json").then((res) => {
    axios.get(LinksAPI.footerData).then((res) => {
      setCreatorName(res.data.name);
      setCreatorLink(res.data.link);
      setDate(res.data.date);
    });
  }, []);
  return (
    <FooterSection>
      <FooterSectionDesc>
        {" "}
        <FooterDate>{date}</FooterDate> by{" "}
        <a href={creatorLink}>{creatorName} </a>{" "}
      </FooterSectionDesc>
    </FooterSection>
  );
};
export default Footer;
