// import { HashRouter } from "react-router-dom";
const container = document.getElementById("root");

const root = createRoot(container); // createRoot(container!) if you use TypeScript
root.render(
  <React.StrictMode>
    <Provider store={store}>
      {/* <HashRouter> */}
      <App />
      {/* </HashRouter> */}
    </Provider>
  </React.StrictMode>
);

