import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import ComponentsAPI from "./APIs/componentsAPI";
// import PageNav from "./components/PageNav";
// import Home from "./components/Home";
// import Portfolio from "./components/Portfolio";
// import Footer from "./components/Footer";
// import Work from "./components/Work";

// const Login = ComponentsAPI.Login;
const Header = () => {
  return (
    <div id="body">
      <Router>
        <Switch>
          {/* <Route exact path="/" component={ComponentsAPI.Logout} />; */}
          <Route exact path="/home" component={ComponentsAPI.Home} />;
          <Route exact path="/portfolio" component={ComponentsAPI.Portfolio} />
          ;
          <Route exact path="/work" component={ComponentsAPI.HomeItems} />;
          <Route exact path={["/", "/login"]} component={ComponentsAPI.Login} />
          ;
          <Route exact path="/register" component={ComponentsAPI.Register} />;
          <Route
            exact
            path="/notAuthorized"
            component={ComponentsAPI.NotAuthorized}
          />
          ;
          <Route exact path="/upload" component={ComponentsAPI.UploadFiles} />;
          {/* <Route exact path="/" component={ComponentsAPI.Main} />; */}
          <Route
            exact
            path="/tutorials"
            component={ComponentsAPI.TutorialsList}
          />
          <Route
            exact
            path="/tutorials/add"
            component={ComponentsAPI.AddTutorial}
          />
          <Route path="/tutorials/:id" component={ComponentsAPI.Tutorial} />
          {/* Test component */}
          <Route path="/test" component={ComponentsAPI.SignupForm} />
        </Switch>
      </Router>
    </div>
  );
};

export default Header;
