import React from "react";
import "./navbar.css";

// import { useDispatch, useSelector } from "react-redux";

// import "bootstrap/dist/css/bootstrap.min.css";
import ComponentsAPI from "../../APIs/componentsAPI";
// import "./AuthApp.css";

// import eventBus from "../common/EventBus";
// import { authSlice } from "../../slices";
// import { Redirect } from "react-router-dom";
// // import { Redirect } from "react-router-dom";

const NavBar = ({ currentUser, logOut }) => {
  return (
    <nav className="navbar">
      <div className="logo">Supplies for Life</div>

      <ul className="nav-links">
        <input type="checkbox" id="checkbox_toggle" />
        <label htmlFor="checkbox_toggle" className="hamburger">
          &#9776;
        </label>
        <div className="menu">
          <li>
            <a href="/home">Home</a>
          </li>
          <li>
            <a href="/work">Work</a>
          </li>
          <li>
            <a href="/portfolio">Portfolio</a>
          </li>
          {/* <li className="services">
            <a href="/#">Posts</a>
            <ul className="dropdown">
              <li>
                <a href="/tutorials">All Posts</a>
              </li>
              <li>
                <a href="/tutorials/add">New Post</a>
              </li>
              <li>
                <a href="/tutorials/:id">Show Post</a>
              </li>
            </ul>
          </li> */}
          <li className="services">
            <a href="/#">Products</a>
            <ul className="dropdown">
              <li>
                <a href="/product1">Product 1 </a>
              </li>
              <li>
                <a href="/product2">Product 2</a>
              </li>
              <li>
                <a href="/product3">Product 3</a>
              </li>
              <li>
                <a href="/product4">Product 4</a>
              </li>
              <li>
                <a href="/product5">Product 5</a>
              </li>
            </ul>
          </li>
          <li>
            <a href="/contact">Contact</a>
          </li>
          <li>
            <a href="/upload">Image</a>
          </li>
          <li>
            <a href="/test">Test</a>
          </li>
          {currentUser && currentUser != "" ? (
            <>
              <ComponentsAPI.AdminUserUI
                currentUser={currentUser}
                logOut={logOut}
              />
            </>
          ) : (
            <>
              <ComponentsAPI.NormalUserUI />
            </>
          )}{" "}
        </div>
      </ul>
    </nav>
  );
};

export default NavBar;
